import axios from "axios";
import { useState } from "react";
import LoginAndRegistration from "./LoginAndRegistration";
import MainPage from "./MainPage";

function App() {
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [regUserName, setRegUserName] = useState("");
  const [regPassword, setRegPassword] = useState("");
  const [token, setToken] = useState("");
  const [secretData, setSecretData] = useState("");
  const [loggedIn, setLoggedIn] = useState(false);
  const [registrationStatus, setRegistrationStatus] = useState("");

  const handleSubmit = async (event) => {
    event.preventDefault();
    console.log("username:", userName);
    console.log("password:", password);
    let response = await axios.post("http://localhost:8000/login", {
      username: userName,
      password: password,
    });
    localStorage.setItem("jwtToken", response.data);
    setToken(response.data);
    setLoggedIn(true);
    console.log(response.data);
    requestBalance();
  };

  const handleRegistrationSubmit = async (event) => {
    event.preventDefault();
    let response = await axios.post("http://localhost:8000/register", {
      username: regUserName,
      password: regPassword,
    });
    setRegistrationStatus(response.data);
  };

  const requestBalance = async () => {
    let getToken = localStorage.getItem("jwtToken");
    let response = await axios.get("http://localhost:8000/balance", {
      headers: {
        authorization: "bearer " + getToken,
      },
    });
    setSecretData(response.data);
  };

  return (
    <div>
      {!loggedIn ? (
        <LoginAndRegistration
          password={password}
          setPassword={setPassword}
          handleSubmit={handleSubmit}
          handleRegistrationSubmit={handleRegistrationSubmit}
          token={token}
          userName={userName}
          setUserName={setUserName}
          regUserName={regUserName}
          setRegUserName={setRegUserName}
          regPassword={regPassword}
          setRegPassword={setRegPassword}
        />
      ) : (
        <MainPage balance={secretData}/>
      )}
    </div>
  );
}

export default App;
